import React from 'react'
import Footer from '../Footer'
import Sidebar from '../Sidebar'

function AboutPage() {
    return (
        <div className="about-page-container">
            <Sidebar home={false}/>
            
            <h1 className="page-header">About</h1>
            <div className="about-page-text">
                <p>
                    My name is Chris Kim. I'm a sophomore at Cornell University.
                    {/* College of Arts and Sciences at Cornell University. I haven't 
                    declared a major yet, but I'm heavily favoring Computer Science. 
                    I am interested in working in the tech industry. I've recently been introduced to Computer Science. In 
                    fact, I took my first course in CS (CS 1110) just last fall (Fall 2020). Before that,
                    I really had no experience with Computer Science or programming. */}
                    
                    <br/>

                    {/* Outside of academics I enjoy playing piano, swimming, and photography.
                    While I'm not an expert in piano, I can say pretty confidently
                    say that I am a Chopin fan. */}
                    
                    <br/>

                    This website is in and of itself a project. It's built entirely with React, styled
                    with CSS, and hosted on Github Pages for free.
                </p>
            </div>

            <Footer page="about"/>
        </div>
    )
}

export default AboutPage
